@import "../../styles/AppDesign.scss";
$transTime: .6s;
.gallery-slider {
    position: relative;
    width: 100%;
    margin:0 auto;
    // &:before{
    //     content: '';
    //     display:block;
    //     padding-bottom:100%;
    //     background: rgba(#000, 0.5);
    // }
    @include mobileView(){
        width: 75%;
        margin:0;
        @media (max-width: 480px) {
            width: 80%;
        }
    }
}

.gallery-slider__slider{
    // width: 200px;
    // display: flex;
    // align-items: center;
    // margin:0 auto;
    position: relative;
    // left:-15%;
    width: 100%;
    margin:0 auto;
    font-size: 0;
    &:before{
        content: '';
        display:block;
        padding-bottom:62.5%;
    }
    @include mobileView(){
        left:0%;
    }
}
.gallery-slider__slide{
    position: absolute;
    width: 100%;
    height:100%;
    right:0;
    top:0;
    // position: relative;
    // width: 10vw;
    // display: flex;
    // justify-content: center;
    // margin:0 auto;
    z-index: 10;
    order:1;
    transition: right .3s, transform .3s, opacity .15s;
    &.is-active{
        z-index: 30;
        order: 0 !important;
        transition: right $transTime;
    }
    &.is-prev{
        z-index: 40;
        right: 25% !important;
        transition: right .3s;
        // opacity:.6;
    }
    &:hover:not(.is-active){
        transform: translate(5px);
    }
}
.gallery-slider__slide-body{
    position: absolute;
    width: 100%;
    height:100%;
    left:0;
    top:0;
    transition: $transTime;
    transform-origin: right center;
}
.gallery-slider__click-area{
    cursor: pointer;
    border:0;
    padding:0;
    background: transparent;
    font-size: 0;
    opacity: 0;
    position: absolute;
    z-index: 30;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    .gallery-slider__slide.is-active &{
        pointer-events: none;
    }
}
.gallery-slider__img{
    // position: relative;
    // width: 33.3333vw;
    // flex: 0 0 auto;
    position: absolute;
    width: 100%;
    height:100%;
    left:0;
    top:0;
    background: no-repeat center center;
    background-size: cover;
    transition: $transTime;
    pointer-events: none;
    // &:before{
    //     content: '';
    //     display:block;
    //     padding-bottom:75%;
    // }
    transform: perspective(1000px) rotateY(-10deg);
    left:-3%;
    .gallery-slider__slide.is-active &{
        transform: perspective(1000px) rotateY(-10deg);
        left:-3%;
    }
    // .gallery-slider__slide.is-active:hover &{
    //     transform: none;
    // }
}
.gallery-slider__control{
    margin-top:30px;
    position: relative;
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.gallery-slider__prev,
.gallery-slider__next{
    width: 25px;
    height: 32px;
    appearance: none;
    background: url('/img/icon/icon-gallery-next.svg') no-repeat center center transparent;
    background-size: contain;
    border: 0;
    cursor: pointer;
    &:disabled {
        opacity:.2;
        pointer-events: none;
    }
}
.gallery-slider__prev{
    transform: rotate(180deg);
}
.gallery-slider__page{
    text-align: center;
    color:$main;
    @include exhibitionDetailTypo();
}