@import "../../styles/AppDesign.scss";

.main-layout {
  overflow:auto;
  width:100%;
}
.main-layout__body{
  width:100%;
  min-height: 100vh;
  // background:$main;
  background: $bgWhite;
  &:after {
      content: '';
      display:block;
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100vw;
      background: url('/img/landing/bg-curve-red.svg') no-repeat right top;
      background-size: 100% auto;
      @include mobileView() {
        background-image: url('/img/landing/bg-curve-red-mobile.svg');
      }
      transition: .5s;
      z-index: 550;
      pointer-events: none;
      .after-video &{
        display:none;
        background-image: url('/img/landing/bg-curve.svg');
        @include mobileView() {
          background-image: url('/img/landing/bg-curve-mobile.svg');
        }
      }
      .after-video.after-exhibition &{
        display:block;
      }
      .color-inversed &{
        background-image: url('/img/landing/bg-curve-white.svg'); 
        @include mobileView() {
          background-image: url('/img/landing/bg-curve-white-mobile.svg');
        }
      }
      .main-layout.page-ready:not(.in-video):not(.after-video) &{
        display:none;
      }
  }
}
.main-layout__section-spacer{
  display:block;
  @include secSpacer();
}