@import "../../styles/AppDesign.scss";

.shapeout-text {
    position: relative;
    width: 100%;
}

.shapeout-text__body {
    position: relative;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
}

.shapeout-text__line {
    position: relative;
    display: flex;
    overflow:hidden;
}

.shapeout-text__content {
    position: relative;
    display: inline-block;
    overflow: hidden;
    line-height: 1.125;

    >* {
        opacity: 0;
        // transform: translateX(100%);
        z-index: 10;
        @include secTitleTypo();
        // opacity: 1;
    }
}

.shapeout-text__txt-alternative{
    position: relative;
    overflow: hidden;
    height: 1em;
    span{
        display: block;
    }
    .shapeout-text__txt-alternative_inner{
        position: relative;
        height: 1em;
        span:nth-child(2){
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            transition-property: opacity;
            transition-duration: 0.25s;
            transition-delay: 0.25s;
        }
    }
    &::before{
        content: '';
        position: absolute;
        display: block;
        overflow: hidden;
        width: 65vw;
        height: 90%;
        top: 5%;
        transform-origin: center center;
        transform: skewX(25deg) scaleY(1) translate3d(-105%, -20%, 0);
        opacity: 1;
        z-index: 20;
        transition: 0s;

    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+2,d8182a+20,d8182a+80,ffffff+98&0+2,1+20,1+80,0+98 */
    background: -moz-linear-gradient(left,  rgba(255,255,255,0) 2%, rgba(216,24,42,1) 20%, rgba(216,24,42,1) 80%, rgba(255,255,255,0) 98%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left,  rgba(255,255,255,0) 2%,rgba(216,24,42,1) 20%,rgba(216,24,42,1) 80%,rgba(255,255,255,0) 98%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right,  rgba(255,255,255,0) 2%,rgba(216,24,42,1) 20%,rgba(216,24,42,1) 80%,rgba(255,255,255,0) 98%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#00ffffff',GradientType=1 ); /* IE6-9 */
    }
}

.shapeout-text.is-active{
    .shapeout-text__txt-alternative.queued{
        &:before{
            transition: 1.3s;
            //transform: scaleY(0) translateX(100%);
            transform: skewX(25deg) scaleY(0.0001) translate3d(65%, -20%, 0);
        }
        .shapeout-text__txt-alternative_inner{
            span:nth-child(1){
                opacity: 0;
                transition: opacity 0.25s;
            }
            span:nth-child(2){
                opacity: 1;
            }
        }
    }
}

