    position: absolute;
    display: block;
    overflow: hidden;
    width: 65vw;
    height: 90%;
    top: 5%;
    transform-origin: center center;
    transform: skewX(25deg) scaleY(1) translate3d(-105%, -20%, 0);
    opacity: 1;
    z-index: 20;
    transition: 0s;

