.shapeout-text.is-active {
    .shapeout-text__shape {
        transition: 1.3s;
        //transform: scaleY(0) translateX(100%);
        transform: skewX(25deg) scaleY(0.0001) translate3d(105%, -20%, 0);

    }

    .shapeout-text__content {
        >* {
            transition: .3s .5s;
            opacity: 1;
            transform: translateX(0);
        }
    }

    .shapeout-text__line:nth-child(2) {
        .shapeout-text__shape {
            transition-delay: .3s;
        }

        .shapeout-text__content>* {
            transition-delay: .8s;
        }
    }

    .shapeout-text__line:nth-child(3) {
        .shapeout-text__shape {
            transition-delay: .6s;
        }

        .shapeout-text__content>* {
            transition-delay: 1.1s;
        }
    }

    .shapeout-text__line:nth-child(4) {
        .shapeout-text__shape {
            transition-delay: .9s;
        }

        .shapeout-text__content>* {
            transition-delay: 1.4s;
        }
    }
}