@import "../../styles/AppDesign.scss";

.splash {
    position: fixed;
    height: 100vh;
    width: 100%;
    z-index: 1500;
    width: 100vw;
    top: 0;
    left: 0;

    &.splash--ref {
        background: #FFF;
        pointer-events: all !important;
    }

    //transition: .4s;
    .page-ready & {
        pointer-events: none;
        animation: splash-fadeout 1s forwards;
        @keyframes splash-fadeout {
            0% {
                opacity: 1;
            }

            100% {
                opacity: 0;
                transform: scale(1.2);
            }
        }
        // fadeout in design2
    }

    &:before {
        content: '';
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100vw;
        background: url('/img/landing/bg-curve-red.svg') no-repeat right top;
        background-color: $bgWhite;
        background-size: 100% auto;

        @include mobileView() {
            background-image: url('/img/landing/bg-curve-red-mobile.svg');
        }

        z-index: 0;
        pointer-events: none;

        // .page-ready & {
        //     display: none;
        // }
    }
}

.splash__overlay {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 150vw;
    background-color: $bgWhite;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f5f5f5+5,f5f5f5+98&0+5,1+12 */
    background: -moz-linear-gradient(left, rgba(245, 245, 245, 0) 5%, rgba(245, 245, 245, 1) 12%, rgba(245, 245, 245, 1) 98%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(left, rgba(245, 245, 245, 0) 5%, rgba(245, 245, 245, 1) 12%, rgba(245, 245, 245, 1) 98%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, rgba(245, 245, 245, 0) 5%, rgba(245, 245, 245, 1) 12%, rgba(245, 245, 245, 1) 98%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00f5f5f5', endColorstr='#f5f5f5', GradientType=1);
    /* IE6-9 */

    z-index: 5;
    animation: splash-overlay-move ease-out forwards;

    @keyframes splash-overlay-move {
        0% {
            transform: translateX(0);
        }

        100% {
            transform: translateX(100%);
        }
    }

    .page-ready & {
        display: none;
    }
}

.splash__inner {
    position: absolute;
    height: 100%;
    width: 100%;
    right: 0;
    top: 0;
    z-index: 30;
    transform-origin: right top;
    display: flex;
    align-items: center;
    justify-content: center;

    .splash--step-3 & {
        $logoStaticSize: 157.16px;
        $logoStaticSizeM: 97.16px;

        @keyframes logo-move {
            0% {
                transform: scale(1);
            }

            75% {
                transform: scale(1);
            }

            100% {
                opacity: 0;
                // height: $logoStaticSize * 0.8;
                // width: $logoStaticSize;
                // margin-right: 5px;
            }
        }

        .splash-finished & {
            // animation: logo-move $ani-time-splash-step3 forwards;// animation: logo-fadeout 1s forwards;
            // pointer-events: none;
        }

        @include tabletView() {
            @keyframes logo-move-mb {
                0% {
                    transform: scale(1);
                }

                75% {
                    transform: scale(1);
                }

                100% {
                    opacity: 0;
                    // height: $logoStaticSizeM * 0.8;
                    // width: $logoStaticSizeM;
                    // margin-right: 5px;
                }
            }

            // animation: logo-move-mb $ani-time-splash-step3 forwards;
            // .splash-finished &{
            //     animation: logo-move-mb $ani-time-splash-step3 forwards;
            // }
            opacity:0;
            pointer-events: none;
        }
    }


    .splash--ref & {
        animation: none !important;
    }
}

.splash__logo {
    width: 70%;
    max-width: 640px;
    transform: scale(1);

    .splash--step-0 & {
        opacity: 0;
    }

    .splash--step-1 & {
        opacity: 1;

        @keyframes logo-enlarge {
            0% {
                transform: scale(0.25);
            }

            100% {
                transform: scale(1);
            }
        }

        animation: logo-enlarge $ani-time-splash-step1;
    }

    .splash--step-2 & {
        opacity: 1;
    }

    .splash--step-3 & {
        opacity: 1;
    }

    .color-inveresed.splash-finished & {
        >* {
            transition: .1s;
            transition-delay: .4s;
            opacity: 0;

            &.splash__static-logo {
                opacity: 1;
            }
        }
    }
}

.splash__static-logo {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: url('/img/logo/logo-main-white.svg') no-repeat center center;
    background-size: contain;
    opacity: 0;

    .main-layout:not(.in-video) .sidebar.sidebar--menu-open+.main-layout__main & {
        opacity: 1;
    }

    .color-inveresed & {
        background-image: url('/img/logo/logo-main-white-text.svg');
    }
}

.splash__ref {
    @extend .splash__static-logo;
    opacity: .5;
    z-index: 15;
    background-size: 174.6%;

    &--00 {
        background-size: 245.6%;
    }

    &--15 {
        background-size: 174.6%;
    }

    &--29 {
        background-size: 153.4%;
    }

    &--30 {
        background-size: 138.1%;
    }

    &--38 {
        background-size: 125.1%;
    }

    &--48 {
        background-size: 114.2%;
    }

    &--50 {
        background-size: 100%;
    }

    .splash:not(.splash--ref) & {
        display: none !important;
    }
}