@import "../../styles/AppDesign.scss";

.landing {
    position: relative;
    height: 100vh;
    width: 100%;
    z-index: 500;
    .after-video &{
        // overflow: hidden;
        opacity:0;
        transition: opacity .3s;
    }
    top: 0;
    left: 0;
    &:before {
        content: '';
        position: absolute;
        top:0;
        left: 0;
        display: block;
        width:100%;
        height:100px;
        pointer-events: none;
        // background-image: linear-gradient(180deg, #000000 0%, rgba(0,0,0,0.00) 100%);
        background-image: linear-gradient(180deg, #CFD3D6 0%, rgba(207,211,214,0.00) 100%);
        z-index: 305;
    }
    &:after {
        content: '';
        position: fixed;
        top:0;
        left: 0;
        display: block;
        width:100px;
        height:100%;
        pointer-events: none;
        //background-image: linear-gradient(90deg, #000000 0%, rgba(0,0,0,0.00) 100%);
        background-image: linear-gradient(90deg, #CFD3D6 0%, rgba(207,211,214,0.00) 100%);
        z-index: 305;
        .after-video &{
            position: absolute;
            display:none;
        }
    }
}
// $zoomTime: 2s;
// $zoomTimeDelay: .1s;
.landing__bg{
    position: fixed;
    height: 130%;
    width: 130%;
    background: url(/img/landing/bg-landing.jpg) no-repeat center center;
    background-size: cover;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%) scale(1);
    z-index: 300;
    // transition: transform $zoomTime cubic-bezier(0.885, 0.370, 0.930, 0.685);
    transition: transform 1.6s cubic-bezier(.43,.195,.02,1);
    .page-ready &{
        transform: translate(-50%, -50%) scale(.8);
    }
    @include mobileView() {
        height: 115%;
        width: 115%;
        transform: translate(-50%, -50%) scale(1);
        background-image: url(/img/landing/bg-landing-m.jpg);
        .page-ready &{
            transform: translate(-50%, -50%) scale(.9);
        }
    }


