.landing__inner {
    position: fixed;
    // background:$main;
    //background: $bgWhite;
    top: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    opacity: 0;
    z-index: 310;

    @include mobileView() {
        padding-left: 60px;
        padding-right: 30px;
    }

    @include navAndSloganTopPadding(0, 15px, 0);

    .landing.is-active & {
        .page-ready & {
            opacity: 1;
        }
    }
}

.landing__slogans {
    @include mobileView() {
        width: 100%;
    }
}

.landing__slogan {
    display: block;
    opacity: 0;
    $initialDelay: .15s;
    $delayInterval: .15s;
    transform: translateY(200%);
    // transition: transform .5s, opacity .7s;
    height: 5.9vw;

    @include tabletView() {
        height: 6.5vw;
    }

    @include mobileView() {
        height: 9.2vw;
    }

    font-size:0;

    &-holder {
        height: 100%;
        display: inline-block;
    }

    &-img {
        display: block;
        height: 100%;
        width: auto;
        // filter: invert(1);
    }

    .landing.is-active & {
        .page-ready & {
            // opacity: 1;
            // transform: translateY(0);
            animation: landing-slogan-move .5s forwards;

            &--1 {
                animation-delay: $initialDelay + $delayInterval * 0;
                margin-top: -2vh;

                @include tabletView() {
                    margin-top: -2vw;
                }
            }

            &--2 {
                animation-delay: $initialDelay + $delayInterval * 1;
            }

            &--3 {
                animation-delay: $initialDelay + $delayInterval * 2;
            }
        }
    }

    @keyframes landing-slogan-move {
        0% {
            opacity: 0;
            transform: translateY(100%);
        }

        25% {
            opacity: 0.1;
        }

        85% {
            opacity: 0.3;
        }

        100% {
            opacity: 1;
            transform: translateY(0);
        }
    }


    // &--1 {
    //     animation-delay: $initialDelay + $delayInterval * 0;
    // }

    // &--2 {
    //     animation-delay: $initialDelay + $delayInterval * 1;
    // }

    &--3 {
        margin-top: 3vh;

        @media (max-width: 1280px) {
            margin-top: 2.25vh;
        }

        @include tabletView() {
            margin-top: 2.3vw;
        }

        @include mobileView() {
            margin-top: 3vw;
        }

        // @media (max-width: 480px) {
        //     margin-top: 1.45vw;
        // }
    }
}


.landing__slogan-holder {
    position: relative;

    overflow: hidden;
}

.landing__slogan-shape {
    content: '';
    position: absolute;
    display: block;
    overflow: hidden;
    width: 65vw;
    height: 90%;
    top: 5%;
    transform-origin: center center;
    transform: skewX(30deg) scaleY(1) translate3d(-105%, 0%, 0);
    opacity: 1;
    z-index: 20;
    transition: 0s;

    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+2,d8182a+20,d8182a+80,ffffff+98&0+2,1+20,1+80,0+98 */
    background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 2%, rgba(216, 24, 42, 1) 20%, rgba(216, 24, 42, 1) 80%, rgba(255, 255, 255, 0) 98%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 2%, rgba(216, 24, 42, 1) 20%, rgba(216, 24, 42, 1) 80%, rgba(255, 255, 255, 0) 98%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 2%, rgba(216, 24, 42, 1) 20%, rgba(216, 24, 42, 1) 80%, rgba(255, 255, 255, 0) 98%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#00ffffff', GradientType=1);
    /* IE6-9 */

    // display:none; //hidden for v2
    .landing__slogan:nth-child(2) & {
        transform: skewX(30deg) scaleY(1) translate3d(-105%, 30%, 0);
    }

}



.landing.is-active {
    .page-ready & {
        .landing__slogan-shape {
            transition: 1.5s;
            @include mobileView(){
                transition-duration: 1.2s;
            }
            //transform: scaleY(0) translateX(100%);
            transform: skewX(30deg) scaleY(0.01) translate3d(70vw, 0%, 0);
            transition-delay: .4s;
        }

        .landing__slogan:nth-child(2) {
            .landing__slogan-shape {
                transform: skewX(30deg) scaleY(0.01) translate3d(70vw, 30%, 0);
                transition-delay: .6s;
            }
        }

        .landing__slogan:nth-child(3) {
            .landing__slogan-shape {
                transition-delay: .8s;
            }
        }
    }
}