@import "../../styles/AppDesign.scss";
$sidebarWidth:60px;
$sidebarWidthTb:50px;
$sidebarWidthMb:50px;

.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  z-index: 1000;
  padding: 25px;
  opacity: 0;
  pointer-events: none;
  width: $sidebarWidth;

  @include tabletView() {
    width: $sidebarWidthTb;
    padding: 15px 15px;
  }

  @include mobileView() {
    width: $sidebarWidthMb;
    padding: 0 17.5px;
  }

  .page-ready & {
    opacity: 1;
    pointer-events: all;
  }

  &:after {
    content: '';
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    width: 1px;
    height: 100%;
    background: $lineGray;
    transform-origin: center top;
    transition: 1.5s ease-in;
    transform: scale(0);
    z-index: 20;

    .page-ready & {
      transform: scale(1);
    }
  }

  &.sidebar--inverse-color:not(.sidebar--menu-open) {
    color: #FFF;

    &:after {
      background: rgba(#FFF, .25);
    }
  }

  &.sidebar--menu-open{
    z-index: 1500;
  }
  .in-video & {
      opacity: 0;
      pointer-events: none;
  }
}
