.sidebar__overlay {
  position: fixed;
  z-index: 10;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  transition: .4s;
  background: transparent;
  pointer-events: none;

  .sidebar--menu-open & {
    background: $main;
    pointer-events: all;
  }

  @include desktopView() {
    display: none !important;
  }
}

.sidebar__inner {
  position: relative;
  z-index: 25;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 15px;
  @include navAndSloganTopPadding(-25px, 0, 0);
}

.sidebar__top {
  position: fixed;
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-start;
  z-index: 1200;
  left: 75px;
  top: 20px;
  @include tabletView() {
    left: 15px;
    top: 20px;
  }
  @include mobileView() {
    left: 17.5px;
    top: 0;
  }
  .sidebar &{
    position: absolute;
    opacity:0.5;
    top: 0;
    left: 0;
    display:none;
  }
  .in-video & {
      opacity: 0;
      pointer-events: none;
  }

  &.sidebar--menu-open{
    z-index: 1550;
  }
  @include mobileView() {
    padding-top: 20px;
  }
}

.sidebar__menu-btn {
  position: relative;
  border: 0;
  background: transparent;
  padding: 0;
  cursor: pointer;
  opacity: 0;

  .page-ready & {
    opacity: 1;
  }

  margin-right: 20px;

  @include mobileView() {
    margin-right: 23px;
  }

  @include desktopView() {
    display: none !important;
  }
}

.sidebar__menu-text {
  @include a11y-hidden();
}

.sidebar__menu-icon {
  $iconColor: $main;
  $lineHeight: 2px;

  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: .4s;

  $size: 25px;
  width: $size;
  height: $size;

  @include smallScreen() {
    $size: 20px;
    width: $size;
    height: $size;
  }

  &:before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    left: 0;
    top: 50%;
    height: $lineHeight;
    margin-top: -$lineHeight/2;
    transition: .4s;
    background: $iconColor;

    .sidebar--menu-open & {
      background: #FFF;
    }

    .sidebar--inverse-color:not(.sidebar--menu-open) & {
      background: $main;
    }
  }

  .sidebar__menu-lines {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      left: 0;
      height: $lineHeight;
      // margin-top:-$lineHeight/2;
      background: $iconColor;
      transition: .2s;
      transform-origin: left center;

      .sidebar--menu-open & {
        background: #FFF;
      }

      .sidebar--inverse-color:not(.sidebar--menu-open) & {
        background: $main;
      }
    }

    &:before {
      top: 10%;
      transform: scaleX(0.7);
    }

    &:after {
      bottom: 10%;
      transform: scaleX(0.5);
    }
  }

  .sidebar--menu-open & {
    &:before {
      display: none;
    }

    .sidebar__menu-lines {
      transform: rotate(45deg);

      &:before,
      &:after {
        top: 50%;
        left: 0;
        bottom: auto;
        height: $lineHeight;
        margin-top: -$lineHeight/2;
        transform-origin: center center;
        transform: scaleX(1);
      }

      &:after {
        transform: rotate(90deg) scaleX(1);
      }
    }
  }
}

.sidebar__title {
  overflow: hidden;
  @include secSubtitleTypo();

  @include mobileView() {
    letter-spacing: 1px;
    margin-top:-3px;
  }
  .sidebar--inverse-color:not(.sidebar--menu-open) &{
    color: #FFF;
  }
}

.sidebar__title-image{
  position: relative;
  height: 60px;
  // @include tabletView() {
  //   height: 50px;
  // }
  @include mobileView() {
    height: 40px;
  }
  img{
    height:100%;
    opacity:0;
  }
  &:before {
    content: '';
    background: url(/img/logo/footer-logo-colored.svg) no-repeat center center;
    background-size: contain;
    position: absolute;
    display: block;
    width: 100%;
    height:100%;
    left:0;
    top:0;
    opacity:1;
  }
  .sidebar--menu-open &,
  .sidebar--inverse-color &{
    img{
      opacity:1;
    }
    &:before {
      opacity:0;
    }
  }
  .sidebar--inverse-color &{
    @include mobileView() {
      img{
        opacity:0;
      }
      &:before {
        opacity:1;
      }
    }
  }
}

.sidebar__title-fragment {
  white-space: nowrap;
  transform: translateX(-100%);
  transition: .3s;

  .page-ready & {
    transform: translateX(0);
  }

}

.sidebar__navs {
  position: relative;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.sidebar__nav {
  position: relative;
  @include resetBtn();
  padding: 10px 0;
  $size: 22px;
  $sizeS: 16px;
  display: flex;
  align-items: center;

  &:first-child{
    margin-top:-10px;
  }

  &:before,
  &:after {
    content: '';
    display: block;
    width: $size;
    height: $size;
    border-radius: 100%;

    @include mobileView() {
      width: $sizeS;
      height: $sizeS;
    }
  }

  &:before {
    border: 1px solid $lineGray;
    opacity: 0;
    transition: 1s;
    transform: scale(8);

    .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) & {
      border-color: rgba(#FFF, .25);
    }

    .page-ready & {
      transform: scale(1);
      opacity: 1;
    }
  }

  &:after {
    background: $main;
    transform-origin: center center;
    transform: scale(0.5);
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -$size/2;
    margin-top: -$size/2;

    @include mobileView() {
      margin-left: -$sizeS/2;
      margin-top: -$sizeS/2;
    }

    opacity:0;

    .sidebar.sidebar--menu-open & {
      background: #FFF;
    }

    .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) & {
      background: $main;
    }
  }

  $interval:.2s;

  @for $i from 1 through 10 {
    &:nth-child(#{$i}) {
      &:before {
        transition-delay: $interval * ($i - 1);
      }
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  &.is-active,
  &:hover {
    &:after {
      opacity: 1;
    }

    .sidebar__nav-label {
      @include desktopView() {
        pointer-events: all;
        opacity: 1;
      }
    }
  }

  &:hover:not(.is-active) {
    &:after {
      @include tabletView() {
        opacity: 0;
      }
    }
  }

  &.is-active {
    pointer-events: none;

    >* {
      pointer-events: none !important;
    }

    // .sidebar__nav-label{
    //   color:#FFF;
    // }
  }
}

.sidebar__nav-label {
  position: absolute;
  left: 100%;
  top: 50%;
  transform: translateY(-50%);
  white-space: nowrap;
  // margin-left: 10px;
  margin-left: 33px;
  pointer-events: none;
  opacity: 0;
  transition: .4s;
  color: $main;
  text-transform: uppercase;
  padding-top: .25em;
  // font-size: $fontSizeM;
  // @media (max-width: 1280px) {
  //   font-size: $fontSizeS;
  // }
  @include secIndicatorTypo();

  @include tabletView() {
    margin-left: 24px;
  }

  @include mobileView() {
    margin-left: 20px;
    padding-left: 7px;
  }
  .sidebar.sidebar--menu-open & {
    color: #000;
  }
  .sidebar__nav.is-active &{
    .sidebar.sidebar--menu-open & {
      color: #FFF;
    }
  }

  .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) & {
    color: $main;
  }

  .sidebar--menu-open &,
  .sidebar.sidebar--menu-open.sidebar--inverse-color & {
    pointer-events: all;
    opacity: 1;
  }
}

.sidebar__soc {
  display: block;
  @include resetBtn();
  $size: 18px;
  width: $size;
  height: $size;
  font-size: 0;
  background: url() no-repeat center center;
  background-size: contain;
  margin: 25px auto 0;
  transition: color .3s;
  opacity: 0;
  text-decoration: none;
  @media (max-width: 767px) and (max-height: 580px) {
    margin-top:18px;
  }

  .page-ready & {
    animation: basic-fade-in .4s .7s forwards;
    // .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) &{
    //   color:$main;
    // }
  }

  // &:hover{
  //   filter: invert(1);
  // }
  .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) & {
    filter: invert(1);
  }

  &.sidebar__soc--fb {
    background-image: url(/img/icon/icon-soc-fb.svg);
    width: 18px;
    height: 18px;
  }

  &.sidebar__soc--ig {
    background-image: url(/img/icon/icon-soc-ig.svg);
    width: 18px;
    height: 18px;
  }
}
.sidebar__soc-label{
  .sidebar__soc:hover &,
  .sidebar--menu-open & {
    @include sidenavSocTypo();
    color:#000;
    margin-left:50px;
    text-transform: uppercase;
    @include mobileView() {
      margin-left: 45px;
    }
  }
}
.sidebar__lang {
  display: flex;
  align-items: center;
  justify-content: center;
  @include resetBtn();
  $size: 18px;
  width: $size;
  height: $size;
  margin: 25px auto 0;
  font-size:16px;
  .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) & {
    color:#FFF;
  }
  @include mobileView() {
    display:none;
  }

  &.mod-zhs,
  &.mod-zht{
    font-size:0;
    &:before{
      content:'';
      display:block;
      $size: 100%;
      width: $size;
      height: $size;
      background:url(/img/icon/icon-lang-tc.svg) no-repeat center center;
      background-size: contain;
      .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) & {
        filter: invert(1);
      }
    }
  }
  &.mod-zhs:before{
    background-image:url(/img/icon/icon-lang-sc.svg);
  }

}
.sidebar__join {
  @include resetBtn();
  @include secIndicatorTypo();
  white-space: nowrap;
  transform-origin: center top;
  transform: rotate(180deg);
  position: absolute;
  margin-left: .05em;
  min-width: 1.5em;

  &:hover {
    color: $main;

    .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) & {
      color: $main;
    }
  }

  .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) & {
    color: #FFF;
  }

  transition: color .3s;
  opacity:0;

  .page-ready & {
    animation: basic-fade-in .4s .7s forwards;
  }

  @include tabletView() {
    margin-left: .1em;
  }
  @include mobileView() {
    margin-left: -.05em;
  }
}

.sidebar__join-label {
  writing-mode: vertical-lr;
  font-weight: bold;
  // transform: scale(-1, 1);
}

.sidebar__bottom {
  transition: .4s;

  .sidebar:not(.sidebar--menu-open) & {
    .after-video & {
      @include mobileView() {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}
.sidebar__mobile-lang-panel{
  width: 100vw;
  position: absolute;
  top:100%;
  left:0;
  display:none;
  margin-left:45px;
  @include mobileView() {
    .sidebar--menu-open & {
      display:block;
    }
  }
}
.sidebar__mobile-lang{
  @include secIndicatorTypo();
  @include resetBtn();
  display:block;
  white-space: nowrap;
  margin-top:10px;
  text-align: left;

}