@import "../../styles/AppDesign.scss";

footer{
    position:relative;
    background:#1E1E1E;
    z-index: 1300;
    color:#FFF;
}

.footer__inner{
    position: relative;
    width:94%;
    margin: 0 auto;
    @include tabletView(){
        width:90%;
        max-width:calc(100% - 60px);
    }
}
.footer__title{
    // font-size: $fontSizeTitleS;
    @include footerTitleTypo();
    margin:0 0 80px;
}
.footer__top{
    // padding:45px 0 80px;
    padding:60px 0;
    border-bottom:1px solid rgba(#FFF, .2);
    .footer__inner{
        display:flex;
        justify-content: space-between;
        align-items: flex-end;
        @include mobileView(){
            display:block;
        }
    }
}
.footer__soc{
    @include mobileView(){
        margin-bottom: 60px;
    }
}
.footer__hashes{
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom:30px;
    @include mobileView(){
        justify-content: center;
    }
}
.footer__hash{
    @include footerCopyrightTypo();
    color:rgba(#FFF, .5);
    margin-right:15px;
    @include mobileView(){
        font-size:15px;
        margin-right:10px;
    }
}
$btnGutter:20px;
.footer__soc-btns{
    display:flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left:-$btnGutter;
    margin-right:-$btnGutter;
    @include mobileView(){
        justify-content: center;
        margin-left:0;
        margin-right:0;
    }
}
.footer__soc-btn{
    position:relative;
    display:flex;
    align-items: center;
    padding-left:$btnGutter;
    padding-right:$btnGutter;
    @include mobileView(){
        padding-left:0;
        padding-right:0;
        margin:0 30px 15px;
        &:last-child{
            margin-bottom:0;
        }
    }
    &:before{
        content:'';
        display: block;
        width:1px;
        height:20px;
        position: absolute;
        right:0;
        top:50%;
        margin-top: -10px;
        background:rgba(#FFF, .2);
        @include mobileView(){
            display:none;
        }
    }
    &:last-child:before{
        display:none;
    }
}
$iconSize:18px;
.footer__soc-ig,
.footer__soc-fb{
    position: relative;
    color:#FFF;
    text-decoration: none;
    @include footerCopyrightTypo();
    padding-left: $iconSize + 10px;
    @include mobileView(){
        padding-left: $iconSize + 7px;
        font-size:15px;
    }
    &:before{
        content:'';
        display: block;
        width:$iconSize;
        height:$iconSize;
        position: absolute;
        left:0;
        top:50%;
        margin-top: -$iconSize/2;
        background: no-repeat center center;
        background-size: contain;
        filter: invert(1);
    }
}
.footer__soc-ig{
    &:before{
        background-image: url(/img/icon/icon-soc-ig.svg);
    }
}
.footer__soc-fb{
    &:before{
        background-image: url(/img/icon/icon-soc-fb.svg);
    }
}
.footer__collabs{
    display:flex;
    justify-content: center;
}
.footer__collab-grp{
    display:flex;
    flex-flow:column;
    justify-content: space-between;
    width:100%;
    @include mobileView{
        justify-content: flex-start;
    }
    &:first-child:last-child{
        text-align: center;
        width: auto;
        justify-self: center;
        .footer__logo--k11{
            max-width:232px;
            @include tabletView{
                max-width:180px;
            }
            @include mobileView{
                max-width:232px;
            }
        }
    }
}
.footer__collab-title{
    // font-size: $fontSizeS;
    @include footerSubtitleTypo();
    margin:0 0 50px;
    text-transform: uppercase;
    @include mobileView(){
        margin:0 0 25px;
    }
}
.footer__logos{
    display:flex;
    align-items: flex-end;
    .footer__nwd50 &{
        display:flex;
        align-items: center;
    }
    .footer__collab-grp &{
        @include mobileView{
            justify-content: center;
        }
    }
    @media (max-width: 480px) {
        display: block;
    }
}
.footer__logo{
    margin-right:2rem;
    &:last-child{
        margin-right:0;
    }
    &--k11{
        max-width: 180px;
        @include mobileView{
            width:80%;
            max-width: 140px;
        }
    }
    &--ddb{
        max-width: 120px;
        @include mobileView{
            width:70%;
            max-width: 80px;
        }
    }
    &--msl{
        max-width: 120px;
        margin-left:5rem;
        align-self: flex-end;
        @include mobileView{
            margin-left:0;
            margin-top:2rem;
            display: block;
            width:70%;
            max-width: 80px;
        }
    }
    &--nwd50{
        //max-width: 120px;
        width:415px;
        max-width: 415px;
        @include tabletView{
            width:100%;
            // max-width: 80px;
        }
        @include mobileView{
            width:70%;
            // max-width: 80px;
        }
    }
    &--slogans{
        max-width: 180px;
        @include mobileView{
            width:70%;
            max-width: 120px;
        }
    }
    &--tam{
        width:100%;
        max-width: 520px;
        flex: 0 0 auto;
        @include tabletView{
            width: 70%;
        }
        @include mobileView{
            width: 100%;
        }
    }
}
.footer__logo-br{
    height:90px;
    width:1px;
    background:#FFF;
    margin-right:2rem;
    @include mobileView{
        height:70px;
    }
}
.footer__nwd50{
    display:flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom:130px;
    .footer__logos:last-child{
        justify-content: flex-end;
    }
    @media (max-width: 850px) {
        .footer__logos:first-child{
            width:30%;
        }
        .footer__logos:last-child{
            width:70%;
        }
    }
    @include mobileView{
        display:block;
        margin-bottom:25px;
        .footer__logos:first-child{
            width:100%;
            margin-bottom:100px;
        }
        width:100%;
        .footer__logos:last-child{
            justify-content: flex-start;
        }
    }
}
.footer__bottom{
    padding:60px 0 50px;
}
.footer__info{
    width:100%;
    display:flex;
    justify-content: space-between;
    align-items: flex-end;
    @include mobileView{
        display:flex;
        flex-flow: column-reverse;
        align-items: flex-start;
    }
    .footer__logos{
        display:flex;
        justify-content: flex-end;
        @include mobileView{
            justify-content: flex-start;
        }
    }
}
.footer__copyright{
    @include footerCopyrightTypo();
    color:rgba(#FFF, .5);
    @include mobileView{
        margin-top:1rem;
    }
}