@import "../../styles/AppDesign.scss";

.stair-text {
    position: relative;
    width: 100%;
    font-size: 1.3em;

    @include mobileView() {
        font-size: 1em;
    }
}


.stair-text__content {
    $total-lines: 2;
    $left-offset: -40px;
    $clip-height: 0.95em;
    $clip-height-mob: 0.7em;
    $line-height: $clip-height;
    position: relative;
    display: block;
    transform-origin: left top;
    transform: scale(0.75) translate3d(7.5%, 25%, 0);
    -webkit-font-smoothing: antialiased;
    -webkit-font-kerning: normal;
    -webkit-text-size-adjust: 100%;
    width: 150%;

    .stair-text.has-mobile & {
        &.is-desktop {
            display: block;
            @include mobileView() {
                display: none;
            }
        }

        &.is-mobile {
            display: none;
            @include mobileView() {
                display: block;
            }
        }
    }

    @include largeScreen() {
        width: 160%;
        // margin-left: -30%;
        transform: scale(0.75) translate3d(10%, 50%, 0);
    }

    @include tabletView() {
        width: 180%;
        margin-left: 0;
        transform: scale(0.75) translate3d(5vw, 50%, 0);
    }

    @include mobileView() {
        width: 180%;
        transform: scale(1) translate3d(35vw, 75%, 0);
        // transform: scale(0.75) translate3d(15vw, 50%, 0);
        font-size: 9vw;
    }
    @media (max-width: 640px) {
        transform: scale(1) translate3d(30vw, 50%, 0);
        font-size: 10vw;
    }
    @media (max-width: 480px) {
        transform: scale(1) translate3d(30vw, 50%, 0);
        font-size: 13vw;
    }
    // @media (max-width: 400px) {
    //     transform: scale(1) translate3d(55vw, 50%, 0);
    // }
    @media (max-width: 375px) {
        transform: scale(1) translate3d(32vw, 50%, 0);
        // font-size: 9vw;
    }
    @media (max-width: 350px) {
        transform: scale(1) translate3d(35vw, 50%, 0);
        // font-size: 9vw;
    }


    // @media (max-width: 767px) {
    //     width: 140%;
    //     transform: scale(0.9) translate3d(15%, 150%, 0);
    // }

    // @media (max-width: 640px) {
    //     transform: scale(0.9) translate3d(25%, 120%, 0);
    // }

    // @media (max-width: 480px) {
    //     transform: scale(0.9) translate3d(25%, 70%, 0);
    //     font-size: 1em;
    // }

    // @media (max-width: 380px) {
    //     transform: scale(0.75) translate3d(40%, 70%, 0);
    //     font-size: .8em;
    // }

    @include desktopView() {
        .stair-text:hover & {
            .stair-text__txt {
                transform: translate3d(0, -($clip-height), 0);
            }
        }
    }

    @include tabletView() {
        .stair-text:not(.is-active) & {
            .stair-text__txt {
                transform: translate3d(0, -($clip-height), 0);
            }
        }
    }
    @include mobileView() {
        .stair-text:not(.is-active) & {
            .stair-text__txt {
                transform: translate3d(0, -(0.8em), 0);
            }
        }
    }

    .stair-text__line {
        height: $clip-height;
        display: block;
        overflow: hidden;
        position: relative;

        @include mobileView() {
            height: $clip-height-mob;
        }
        &:nth-child(odd) {
            transform: skew(-55deg, 20deg) scaleY(.6);
        }

        &:nth-child(even) {
            transform: skew(-0deg, 20deg) scaleY(1);
        }

        @for $i from 1 through 10 {
            &:nth-child(#{$i}) {
                left: $left-offset * $i;
                @include mobileView() {
                    left: -20px * $i;
                }
            }
        }
    }

    .stair-text__txt {
        display: block;
        height: $clip-height;
        line-height: $line-height;
        transition: all .3s ease-in-out;
        transform: translate3d(0, 0, 0);
        // vertical-align: top;
        white-space: nowrap;
        @include mobileView() {
            height: $clip-height-mob + 0.05em;
        }
    }
}